import React, { useState } from 'react'
import Modal from '@mui/material/Modal'

import './ConfirmAlunoCursos.scss'

export default function ConfirmAlunoCursos({modal, modalClose, course, clean, confirm}) {

  const  [ deletarAluno, setDeletarAluno ] = useState(false)

  return (
    <Modal
      open={modal}
      onClose={modalClose}
    >    
      <div id='alunocurso-container'>
        <div className='box'>
          <div className='details-curso'>
            {course.user} - {course.course}
          </div>
          Deseja deletar o curso do aluno?

          <div className='delete-aluno'>
            <input className='check' type='checkbox' onChange={() => setDeletarAluno(!deletarAluno)}/>
            Deletar aluno também?
          </div>

          <div className='btnContainer'>
            <button className='cancelar' onClick={() => {
                modalClose(false)
                clean('')
              }} 
            >
              Cancelar
            </button>
            <button className='confirmar' onClick={() => confirm(course.user_id, course.user_course_id, deletarAluno)}>Confirmar</button>
          </div>
        </div>
      </div>
    </Modal>
  )
}
